import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
// import router from '@/router'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    heading: 'Latest photos',
    status: 'loading',

    photos: [],
    nextPage: 1,
    isLastPage: false,
    query: '',

    photoView: {},
  },

  mutations: {
    setStatus(state, newStatus) {
      state.status = newStatus
    },

    setHeading(state, newHeading) {
      state.heading = newHeading
    },

    setPhotos(state, newPhotos) {
      state.photos = [...newPhotos]
    },

    addPhotos(state, newPhotos) {
      state.photos = [...state.photos, ...newPhotos]
    },

    incrementNextpage(state) {
      state.nextPage++
    },

    setIsLastPage(state, neww) {
      state.isLastPage = neww
    },

    setQuery(state, q) {
      state.query = q
    },
  },

  actions: {
    fetchPhotos({ commit, state }, { replace = true, heading }) {
      let url = 'https://api.unsplash.com/photos'

      if (state.query) {
        url = `https://api.unsplash.com/search/photos?query=${state.query}`
      }

      // console.log(url, query)

      if (replace) {
        commit('setStatus', 'loading')
      } else {
        commit('setStatus', 'updating')
      }

      axios
        .get(url, { params: { page: state.nextPage, per_page: 8 } })
        .then((res) => {
          if (replace) {
            if (res.data.results) {
              commit('setPhotos', res.data.results)
            } else {
              commit('setPhotos', res.data)
            }
          } else {
            if (res.data.results) {
              commit('addPhotos', res.data.results)
            } else {
              commit('addPhotos', res.data)
            }
          }

          commit('incrementNextpage')

          if (heading) commit('setHeading', heading)

          commit(
            'setIsLastPage',
            +res.headers['x-total'] <= +res.headers['x-per-page'],
          )

          if (res.data.results && res.data.total === 0) {
            commit('setStatus', 'notfound')
          } else {
            commit('setStatus', 'ready')
          }
        })
        .catch((err) => {
          console.log(err)
          commit('setStatus', 'error')
        })
    },
  },
})
